<template>
    <div class="row fullscreen">
        <div class="col">
            <div class="text-h8 q-pa-sm text-bold"> KRead </div>
            <div class="q-pa-xl text-center">
                <div class="text-h6 text-bold text-center">
                     1%    .
                </div>
                <br />
                <div class="text-center">
                    <p>
                        KRead   <br />
                        KReaD       
                        <br />
                             .<br />
                        KRead    KReaD 
                           .
                        <br /><br />
                              
                          .<br />
                              
                         ,<br />
                                 
                           .<br /><br />
                             <br />
                              
                        ,<br />
                               <br />
                                
                        .<br />
                                
                          ,<br />
                                
                        .
                    </p>
                </div>
            </div>
        </div>
        <div class="col bg-blue-grey-1 text-center flex center">
            <div class="q-mx-xl" style="flex: 1">
                <q-form
                    @submit="onSubmit"
                    ref="loginFormRef"
                    class="q-gutter-md"
                >
                    <p class="header-label"></p>

                    <q-input
                        bg-color="white"
                        outlined
                        v-model="name"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '  .',
                            validMaxLength,
                            validDenyEnglish,
                            validDenySign,
                            validDenyNumber,
                        ]"
                    />

                    <p class="header-label"></p>
                    <q-input
                        bg-color="white"
                        outlined
                        v-model="email"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '  .',
                            validIncludeAt,
                            validMaxLength50,
                        ]"
                    />

                    <p class="header-label"></p>

                    <q-card flat bordered class="my-card" @click="onTest">
                        <q-card-section>
                            <div class="text-h6">
                                {{ selectedSchoolName }}
                            </div>
                        </q-card-section>
                    </q-card>

                    <p class="header-label"></p>
                    <q-select
                        filled
                        name="grade"
                        v-model="grade"
                        :options="optionsGrade"
                        option-value="value"
                        option-label="label"
                        option-disable="inactive"
                        :rules="[
                            (val) =>
                                (val && val.label !== '') ||
                                '  .',
                        ]"
                    />

                    <p class="header-label"></p>
                    <q-input
                        bg-color="white"
                        outlined
                        v-model="phoneNumber"
                        :dense="dense"
                        lazy-rules
                        :rules="[
                            (val) =>
                                (val && val.length > 0) ||
                                '  .',
                            validMaxLength,
                            validPhoneNo,
                        ]"
                    />
                </q-form>
                <div class="login-btn q-mt-xl">
                    <q-btn
                        class="text-h6"
                        unelevated
                        rounded
                        color="primary"
                        @click="onSubmit"
                        type="submit"
                        label=" "
                    />
                </div>
            </div>
        </div>
    </div>
    <q-dialog v-model="onPopSchool">
        <q-card style="width: 600px">
            <q-card-section>
                <div class="text-h6">  </div>
            </q-card-section>

            <q-separator />
            <q-select
                class="col q-mt-lg q-mx-lg"
                outlined
                v-model="schoolRegion"
                :options="optionsRegion"
                label=" "
                :rules="[
                    (val) =>
                        (val && val.value !== '') || '  .',
                ]"
            />
            <div class="row">
                <q-input
                    class="col q-mx-lg"
                    v-model="keywordSchool"
                    debounce="1000"
                    filled
                    placeholder=" "
                    hint="  ."
                    :rules="[
                        (val) =>
                            (val && val.length > 0 && val.length < 15) ||
                            '15   .',
                        validDenyEnglish,
                        validDenyNumber,
                        validDenySign,
                    ]"
                >
                    <template v-slot:append>
                        <q-icon name="search" />
                    </template>
                </q-input>
            </div>

            <q-card-section style="max-height: 50vh" class="scroll">
                <q-list bordered>
                    <q-item
                        v-for="sch in arrResSchool"
                        :key="sch.code"
                        class="q-my-sm"
                        @click="onItemSchool(sch)"
                        clickable
                        v-ripple
                    >
                        <!-- <q-item-section avatar>
                          <q-avatar color="primary" text-color="white">
                              {{ sch.letter }}
                          </q-avatar>
                      </q-item-section> -->

                        <q-item-section>
                            <q-item-label>{{ sch.name }}</q-item-label>
                            <q-item-label caption lines="1">{{
                                sch.name
                            }}</q-item-label>
                        </q-item-section>

                        <!-- <q-item-section side>
                          <q-icon name="chat_bubble" color="green" />
                      </q-item-section> -->
                    </q-item>

                    <q-separator />
                </q-list>
            </q-card-section>
        </q-card>
    </q-dialog>
    <q-dialog
        v-model="onPopSchoolSecond"
        transition-show="scale"
        transition-hide="scale"
    >
        <q-card class="bg-teal text-white" style="width: 300px">
            <q-card-section>
                <div class="text-h6">{{ selectedSchoolName }}</div>
            </q-card-section>

            <q-card-section class="q-pt-none">
                  ?
            </q-card-section>

            <q-card-actions align="around" class="bg-white text-teal">
                <q-btn
                    flat
                    label=""
                    @click="
                        () => {
                            onPopSchool = false;
                            onPopSchoolSecond = false;
                        }
                    "
                />
                <q-btn flat label=" " v-close-popup />
            </q-card-actions>
        </q-card>
    </q-dialog>
</template>

<script setup>
import { useQuasar } from 'quasar';
import { ref, reactive, watch } from 'vue';
import { GlobalStore } from '@/store';
import { useRouter } from 'vue-router';
import { loginApi } from '@/api/modules/login';
// import optionsRegion from '@/views/login/optionRegion.json';

const onTest = () => {
    console.log('--onTest--');
    onPopSchool.value = true;
};
const onTest123 = () => {
    console.log('--onTest123--');
    onPopSchoolSecond.value = true;
};
const onClose = () => {
    console.log('--onClose--');
    onPopSchool.value = false;
};

const $q = useQuasar();
const router = useRouter();

const dense = ref(false);
const name = ref('');
const email = ref('');
const grade = ref(); // 
const phoneNumber = ref('');
const loginFormRef = ref(null);

const optionsGrade = [
    {
        value: 1,
        label: '-1',
    },
    {
        value: 2,
        label: '2',
    },
    {
        value: 3,
        label: '3',
    },
];

///////////////////////--------///////////////////////
const onPopSchool = ref(false);
const onPopSchoolSecond = ref(false);
const schoolRegion = ref(null);
const keywordSchool = ref();
const selectedSchoolName = ref('  ');
const selectedSchoolAddress = ref();

watch(onPopSchool, (newonPopSchool, oldonPopSchool) => {
    console.log('--onPopSchool:', newonPopSchool, oldonPopSchool);
    if (newonPopSchool) {
        onPopSchool.value = true;
        // schoolRegion.value = null;
        // keywordSchool.value = '';
        // selectedSchoolName.value = '';
    }
});
watch(keywordSchool, (newSch, oldSch) => {
    console.log('--keywordSchool:', newSch, oldSch);
    console.log('--keywordSchool-validate-:', keywordSchool.value.validate());
});

const onItemSchool = (sch) => {
    console.log('--onSchool--', sch.name);
    selectedSchoolName.value = sch.name;
    selectedSchoolAddress.value = sch.address;
    onPopSchoolSecond.value = true;
};
const funOpenPopResultSchool = () => {
    console.log('--funOpenPopResultSchool--');
};
const arrResSchool = reactive([
    {
        name: '1',
        code: 423,
        address: '.',
    },
    {
        name: '2',
        code: 423,
        address: '.',
    },
    {
        name: '3',
        code: 423,
        address: '.',
    },
    {
        name: '4',
        code: 423,
        address: '.',
    },
    {
        name: '5',
        code: 423,
        address: '.',
    },
    {
        name: '6',
        code: 423,
        address: '.',
    },
    {
        name: '7',
        code: 423,
        address: '.',
    },
    {
        name: '8',
        code: 423,
        address: '.',
    },
    {
        name: '9',
        code: 423,
        address: '.',
    },
    {
        name: '10',
        code: 423,
        address: '.',
    },
    {
        name: '11',
        code: 423,
        address: '.',
    },
    {
        name: '12',
        code: 423,
        address: '.',
    },
    {
        name: '13',
        code: 423,
        address: '.',
    },
]);
const optionsRegion = [
    {
        value: 'C10',
        label: '',
    },
    {
        value: 'B10',
        label: '',
    },
    {
        value: 'D10',
        label: '',
    },
    {
        value: 'E10',
        label: '',
    },
    {
        value: 'F10',
        label: '',
    },
    {
        value: 'G10',
        label: '',
    },
    {
        value: 'H10',
        label: '',
    },
    {
        value: 'I10',
        label: '',
    },
    {
        value: 'J10',
        label: '',
    },
    {
        value: 'K10',
        label: '',
    },
    {
        value: 'M10',
        label: '',
    },
    {
        value: 'N10',
        label: '',
    },
    {
        value: 'P10',
        label: '',
    },
    {
        value: 'Q10',
        label: '',
    },
    {
        value: 'R10',
        label: '',
    },
    {
        value: 'S10',
        label: '',
    },
    {
        value: 'T10',
        label: '',
    },
];

///////////////////////--------///////////////////////
const pattern_kor = /[-|-|-]/; // korean
const pattern_eng = /[a-zA-Z]/; // alphabet
const pattern_spc = /[~!@#$%^&*()_+|<>?:{}]/; // sign
const pattern_num = /[0-9]/; // number

const validMaxLength = (val) =>
    val.length > 13 ? '13  .' : true;

const validMaxLength50 = (val) =>
    val.length > 50 ? '  .' : true;

const validDenyKorean = (val) =>
    pattern_kor.test(val) ? '   .' : true;
const validDenyEnglish = (val) =>
    pattern_eng.test(val) ? '   .' : true;
const validDenySign = (val) =>
    pattern_spc.test(val) ? '   .' : true;
const validDenyNumber = (val) =>
    pattern_num.test(val) ? '   .' : true;

const validIncludeAt = (val) =>
    !(val.includes('@') && val.includes('.'))
        ? '  .'
        : true;

const validPhoneNo = (val) =>
    pattern_eng.test(val) || pattern_spc.test(val) || pattern_kor.test(val)
        ? ' (-)   .'
        : val.length < 9
        ? '  .'
        : true;

///////////////////////----Onclick----///////////////////////

const onSubmit = () => {
    // console.log(grade);
    console.log('--loginFormRef-validate-:', loginFormRef.value.validate());

    loginFormRef.value.validate().then((success) => {
        if (success) {
            const saveCallback = async (data) => {
                if (data) {
                    const globalStore = GlobalStore();

                    globalStore.setUserInfo({
                        name: name.value,
                        email: email.value,
                        grade: grade.value.value,
                        phoneNumber: phoneNumber.value,
                    });
                    router.push('/');
                }
            };
            loginApi(
                {
                    stdntNm: name.value,
                    email: email.value,
                    phoneNo: phoneNumber.value,
                },
                saveCallback,
            );
        } else {
            $q.notify({
                color: 'red-4',
                textColor: 'white',
                icon: 'cloud_done',
                message: '   .',
            });
        }
    });
};
</script>

