<template>
    <q-layout view="hHh lpR fFf">
        <q-page-container>
            <q-page>
                <div class="row fullscreen" style="min-width: 600px">
                    <div class="col" style="min-width: 300px">
                        <div class="text-h8 q-pa-sm text-bold">
                             KRead 
                        </div>
                        <div class="q-pa-xl text-center">
                            <div
                                class="text-h6 text-primary text-bold text-center"
                            >
                                KReaD     
                                <br />
                                    1  UP!
                            </div>
                            <br />
                            <div class="text-center">
                                <p>
                                    " KReaD  
                                       ,  
                                        
                                         
                                     .   &
                                         
                                         
                                         
                                       .
                                </p>
                                <img
                                    src="assets/images/diagnostic/diagnostic_info.png"
                                    style="width: 40vw"
                                />
                                <p>
                                    5~6,    
                                    ,  34    60
                                     .   
                                        
                                    .<br />
                                    <br />
                                         ,
                                      ,   , 
                                        .
                                </p>
                            </div>
                        </div>
                    </div>

                    <div
                        class="col bg-blue-grey-1 q-py-xl text-center"
                        style="min-width: 300px"
                    >
                        <div class="q-mx-xl">
                            <q-form
                                @submit="onSubmit"
                                ref="loginFormRef"
                                class="q-gutter-md"
                            >
                                <q-input
                                    outlined
                                    v-model="wId"
                                    bg-color="white"
                                    label=""
                                    hint=""
                                    :rules="[
                                        (val) =>
                                            (val && val.length > 0) ||
                                            '  .',
                                        validMinLength5,
                                        validMaxLength20,
                                    ]"
                                >
                                    <template v-slot:prepend>
                                        <q-icon name="person" />
                                    </template>
                                </q-input>

                                <q-select
                                    outlined
                                    class="q-mt-lg"
                                    bg-color="white"
                                    v-model="wGrade"
                                    :options="optionsGrade"
                                    option-value="value"
                                    option-label="label"
                                    option-disable="inactive"
                                    label=" "
                                    :rules="[
                                        (val) =>
                                            (val && val.label !== '') ||
                                            '  .',
                                    ]"
                                >
                                    <template v-slot:prepend>
                                        <q-icon name="school" />
                                    </template>
                                </q-select>
                            </q-form>
                            <q-btn
                                size="lg"
                                style="width: 100%"
                                class="q-mt-lg text-h6"
                                color="primary"
                                @click="onSubmit"
                                type="submit"
                                :disabled="disableBtnStart"
                                label=" "
                            />
                            
                        </div>
                    </div>
                </div>
            </q-page>
        </q-page-container>
    </q-layout>
</template>

<script setup>
import { useQuasar } from 'quasar';
import { ref, reactive, watch, computed } from 'vue';
import { GlobalStore } from '@/store';
import { useRouter } from 'vue-router';
import {
    postDiagnosticRegister,
    postDiagnosticSubmit,
} from '@/api/modules/login';
import { schoolApi } from './school_api.js';
import { optionsGrade } from './options_custom.js';

const $q = useQuasar();
const router = useRouter();
const globalStore = GlobalStore();

const loginFormRef = ref(null);
const wId = ref('d_user_01');
const wGrade = ref(); // 
// const wPassword = ref('12345678QQ##');
// const wName = ref('');
// const wEmail = ref('afsd@asfd.com');
// const wPhoneNumber = ref('010-9999-8888');
// const isPwd = ref(true);

///////////////////////--------///////////////////////
const onPopSchool = ref(false);
const onPopSchoolSecond = ref(false);
const schoolRegion = ref(null);
const keywordSchool = ref();
const showSearchInfo = ref(false);
const disableBtnStart = ref(false);
const searchInfo = ref(' .');
const selectedSchool = reactive({
    obj: {},
});
const arrResSchool = reactive({
    state: [],
    arr: [],
});

// watch(showSearchInfo, (newshowSearchInfo, oldshowSearchInfo) => {
//     console.log('--onPopSchool:', newshowSearchInfo, oldshowSearchInfo);

// });
watch(onPopSchool, (newonPopSchool, oldonPopSchool) => {
    // console.log('--onPopSchool:', newonPopSchool, oldonPopSchool);
    if (newonPopSchool) {
        selectedSchool.obj = {};
        schoolRegion.value = null;
        keywordSchool.value = '';
        arrResSchool.arr = [];
    }
});
watch(schoolRegion, (nschoolRegion, oschoolRegion) => {
    console.log('--schoolRegion:', nschoolRegion, oschoolRegion);
    // console.log('--keywordSchool-validate-:', keywordSchool.value.validate());
    onSearchSchool();
});
watch(keywordSchool, (newSch, oldSch) => {
    console.log('--keywordSchool:', newSch, oldSch);
    // console.log('--keywordSchool-validate-:', keywordSchool.value.validate());
    onSearchSchool();
});
const onItemSchool = (itemSch) => {
    selectedSchool.obj = itemSch;
    console.log('--selectedSchool.obj--', selectedSchool.obj.SCHUL_NM);
    onPopSchoolSecond.value = true;
};

const onSearchSchool = () => {
    if (!schoolRegion.value || !keywordSchool.value) return;

    console.log('--onSearchSchool:', schoolRegion.value, keywordSchool.value);

    const payload = {
        ATPT_OFCDC_SC_CODE: schoolRegion.value.ATPT_OFCDC_SC_CODE,
        SCHUL_NM: keywordSchool.value,
        type: 'json',
    };
    schoolApi(payload, (data) => {
        if (data) {
            if (!data.schoolInfo) {
                showSearchInfo.value = true;
                searchInfo.value = ' .';
                arrResSchool.arr = [];
                return;
            }

            showSearchInfo.value = false;
            console.log(
                '--onSearchSchool--data.schoolInfo[1].row----:',
                data.schoolInfo[1].row,
            );
            arrResSchool.arr = data.schoolInfo[1].row;
        } else {
            showSearchInfo.value = true;
            searchInfo.value = ' .';
        }
    });
};

///////////////////////--------///////////////////////
const pattern_kor = /[-|-|-]/; // korean
const pattern_eng = /[a-zA-Z]/; // alphabet
const pattern_spc = /[~!@#$%^&*()_+|<>?:{}]/; // sign
const pattern_num = /[0-9]/; // number

const validMinLength5 = (val) =>
    val.length < 5 ? ' 5 .' : true;

const validMaxLength20 = (val) =>
    val.length > 20 ? ' 20 .' : true;

const validDenyKorean = (val) =>
    pattern_kor.test(val) ? '   .' : true;
const validDenyEnglish = (val) =>
    pattern_eng.test(val) ? '   .' : true;
const validDenySign = (val) =>
    pattern_spc.test(val) ? '   .' : true;
const validDenyNumber = (val) =>
    pattern_num.test(val) ? '   .' : true;

const validIncludeAt = (val) =>
    !(val.includes('@') && val.includes('.'))
        ? '  .'
        : true;

const validPhoneNo = (val) =>
    pattern_eng.test(val) || pattern_spc.test(val) || pattern_kor.test(val)
        ? ' (-)   .'
        : val.length < 9
        ? '  .'
        : true;

///////////////////////----Onclick----/////////////////////// 
const onSubmit = () => {
    // disableBtnStart.value = true;
    loginFormRef.value.validate().then((isValid) => {
        if (isValid) {
            postDiagnosticRegister({ id: wId.value }, (rs) => {
                // console.log('--onSubmit rs.data-statusCode-:', rs.data.statusCode);
                if (rs.data) {
                    if (rs.data.statusCode === '200') {
                        console.log(
                            '--onSubmit rs.data-accessToken-:',
                            rs.data.data.accessToken,
                        );

                        globalStore.setToken(rs.data.data.accessToken);
                        globalStore.setVersion(rs.data.data.version);

                        console.log('--onSubmit token--:', globalStore.token);
                        console.log(
                            '--onSubmit version--:',
                            globalStore.version,
                        );
                        globalStore.setUserInfo({
                            id: wId.value,
                            grade: wGrade.value.value,
                            start_kread:
                                wGrade.value.value === 'M1'
                                    ? 1200
                                    : wGrade.value.value === 'M2'
                                    ? 1300
                                    : wGrade.value.value === 'M3'
                                    ? 1400
                                    : -1,
                        });
                        router.push('/diagnostic-evaluation');
                    } else {
                        $q.dialog({
                            message: rs.data.message,
                            persistent: true,
                        });
                    }
                } else {
                    $q.dialog({
                        message: '   ID.',
                        persistent: true,
                    });
                }
            });
        } else {
            $q.notify({
                color: 'red-4',
                textColor: 'white',
                icon: 'cloud_done',
                message: '   .',
            });
        }
    });
};
</script>

<style scoped lang="scss">
.center {
    justify-content: center;
    align-items: center;
}

.header-label {
    // margin-bottom: 8px;
    text-align: left;
    font-weight: 500;
}

.q-item:hover {
    background-color: rgb(238, 242, 255);
}

.login-box {
    display: inline-flex;
    width: 100%;
    position: absolute;
    height: 100%;

    .login-left {
        width: 50%;
        position: relative;
        justify-content: center;
        display: flex;
        top: 50%;
        // align-items: center;

        .login-form {
            width: 80%;
            max-width: 500px;
        }
    }

    .login-right {
        width: 50%;

        .login-icon-box {
            width: 100%;
            height: 100%;
            left: -100px;
            position: absolute;

            // width: 300px;
            // height: 300px;
            // background-image: url('@/assets/images/login_icon.png');
            // background-position: center;
            // background-size: cover;
            img {
                object-fit: contain;
                width: 100%;
                height: 100%;
            }
        }
    }
}

@media (max-width: 800px) {
    .login-box {
        display: flex;

        .login-right {
            display: none;
        }

        .login-left {
            width: 100%;
        }
    }
}
</style>
